import $ from "cash-dom";
import { toggleBg } from "./main";

const popups_container = $('.popups_container');

export function popupToggle(popupEl, actionClass = 'toggleClass'){
	popupEl[actionClass]('active_popup');
	setTimeout(() => popups_container[actionClass]("active"), 50);
	toggleBg(actionClass);
}


$('.dark_bg, .close_popup').on('click', function(){
	const $active_popup = $('.active_popup');
	if ($active_popup.length) {
		popupToggle($active_popup, 'removeClass');
	}
})


$('body').on('click', '.collections_list .collection_item', function(){
	$(this).toggleClass('added_collection');
})