import $ from "cash-dom";
import { getNextPage } from "./api";

const pagination = $('.pagination');
if (pagination.length) {

	pagination.find('.next-page').on('click', () => getNextPage(pagination))

	const pagination_panel_form = pagination.find('.pagination_panel-page_form');
	if (pagination_panel_form.length) {
		pagination_panel_form.on('submit', function(e){
			e.preventDefault();
			const page_input = $(this).find('.pagination_panel-page_input');
			const max = page_input.attr('max');
			const min = page_input.attr('min');
			const this_page = page_input.data('page');
			if (page_input.val() != this_page && page_input.val() >= min && page_input.val() <= max) {
				pagination_panel_form[0].submit();
			}
		})
	}
	
}


	
