import $ from "cash-dom";

$('body').on('click', '.selectors', function(e){
	const _this = $(this);
	if (_this.hasClass('width-parent-emit')) {
		_this.find('.selectors_list').width(_this.parent()[0].clientWidth);
	}

	if (_this.hasClass('child-not-emmit')) {
		if (!$(e.target).parents('.selectors').length) {
			_this.toggleClass('active-selector')
		}
		
	}else{
		_this.toggleClass('active-selector')
	}
})


$('body').on('click', '.selectors .selectors_list__item', function(){
	$(this).siblings().removeClass('selected');
	$(this).addClass('selected');
	const _this = $(this).parents('.selectors');

	const selected_el = _this.find('.selectors_selected');

	if (selected_el.length) {
		const img = $(this).find('img');
		const selected_img = selected_el.find('img');
		if (_this.hasClass('selectors-input')) {
			selected_el.removeClass('empty_selected');
			_this.find('input').val($(this).data('value'));
		}
		if(img.length && selected_img.length){
			selected_img.attr('src', img.attr('src'))
			selected_img.attr('alt', img.attr('alt'))
		}else{
			selected_el.text($(this).find('.selector_value').text());
		}
	}
})
