import $ from "cash-dom";
import { lazyload } from "./lazyload";
import axios from "axios";


//Load images

const createTemplateImageGallery = ({thumbnail, original, labels}) => {
	const {width, height, link: src} = thumbnail;
	const {href_title, title: alt} = labels;
	const {link} = original;

	const template = $('#image_gallery_template');

	const clone = template[0].content.cloneNode(true);

	const p = width / height;

	$(clone).find('.gallery_section__container').css({
		width: `calc(var(--row-height) * ${p})`,
		height: `calc(var(--row-resize) * ${p})`,
	})

	$(clone).find('.gallery_section__link').attr('href', link).attr('title', href_title);

	$(clone).find('.responsive-img').css({
		paddingBottom: `${height * 100 / width}%`
	})


	$(clone).find('.gallery_section__img').attr("data-src", src).attr('width', width).attr('height', height).attr('alt', alt);

	return clone;
}

export const getNextPage = (paginationEl) => {

	const nextPageButton = paginationEl.find('.next-page');
	if (nextPageButton) {
		const page_input =  paginationEl.find('.pagination_panel-page_input');
		const galleryTarget = !!window.galleryFilters ? {...window.galleryFilters, page: window.galleryFilters.page + 1} : {page: page_input.val() + 1};

		axios.post(`/api/gallery?locale=${window.page.locale}`, {
			galleryTarget
		})
		.then(function({galleryTarget, adaptiveGallery}) {
			window.galleryFilters = galleryTarget;

			if (galleryTarget.pages == galleryTarget.page) {
				nextPageButton.hide();
			}

			page_input.data('page', galleryTarget.page).val(galleryTarget.page);
			const gallery = $('.gallery_section-grid');

			const prev = paginationEl.find('.page_prev');
			const next = paginationEl.find('.page_next');

			prev.removeClass('disabled').attr('href', galleryTarget.links.previousPage);
			next.attr('href', galleryTarget.links.nextPage);
			if (!galleryTarget.links.nextPage) {
				next.addClass('disabled')
			}

			for (const key in adaptiveGallery.list) {
				const img = createTemplateImageGallery({...adaptiveGallery.list[key]});
				gallery.append(img);
			}
			lazyload();
		})
		.catch(err => console.log(err))
	}
	
}