export const sprintf = (str, ...argv) => !argv.length ? str : 
sprintf(str = str.replace("%s", argv.shift()), ...argv);

export const randomColor = () => {
	const letters = '0123456789ABCDEF';
	let color = '#';
	for (var i = 0; i < 6; i++) {
		color += letters[Math.floor(Math.random() * 16)];
	}
	return color;
}