import $ from "cash-dom";
import { IS_MOBILE } from "./constants";
import { popupToggle } from "./popups";
import { randomColor } from "./utils";

$.fn.isHidden = function(){
	return this.offsetWidth == 0 && this.offsetHeight == 0;
}


const slider_categories = $('.slider_categories');

if (!IS_MOBILE && slider_categories.length) {

	(($) => {
		 
		const scrollSpeed = 50;
		
		
		
		function startScrolling($list, direction, el, parEl) {
			let scrollValue = 0;
			
			if (direction == 'L') {
				scrollValue -= scrollSpeed;
				
				if ($list.scrollLeft - scrollSpeed  <= -scrollSpeed) {
					scrollValue = 0;
				}

				$list.scrollLeft += scrollValue;
			}else if (direction == 'R') {
				scrollValue += scrollSpeed;
				const max = $list.scrollWidth - $list.offsetWidth;
				if ($list.scrollLeft >= max) {
					scrollValue = max;
				}
		
				$list.scrollLeft += scrollValue;
				
			}
		}

		

		slider_categories.each(function(){

			const slider = $(this);
			const list = slider.find('.slider_categories-list')[0];


			let scrollInterval = null;

			const prev = slider.find('.slider_categories-prev');
			const next = slider.find('.slider_categories-next');

			$(list).on('scroll', function(){

				if (list.scrollLeft - scrollSpeed  <= -scrollSpeed) {
					prev.addClass('hidden_arrow');
				}else{
					prev.removeClass('hidden_arrow');
				}
				const max = list.scrollWidth - list.offsetWidth;
				if (list.scrollLeft >= max) {
					next.addClass('hidden_arrow');
				}else{
					next.removeClass('hidden_arrow');
				}
			});

			$(window).on('resize', function(){
				const max = list.scrollWidth - list.offsetWidth;
				if (max <= 0) {
					prev.addClass('hidden_arrow');
					next.addClass('hidden_arrow');
				}else{
					$(list).trigger('scroll');
				}
			});

			
			
			prev.on('mousedown', () => {
				scrollInterval = setInterval(() => startScrolling(list, 'L', prev, next), 10);
			});
			
			next.on('mousedown', () => {
				scrollInterval = setInterval(() => startScrolling(list, 'R', next, prev), 10);
			});
			
			$(document).on('mouseup', () => {
				clearInterval(scrollInterval);
			});
		})
		setTimeout(() => $(window).trigger('resize'), 100)
		
	})($)
	
}

$('.top_image_action, .collection_action, .like_action').on('click', function(){
	$(this).toggleClass('toggled_action');
	
})

const search_container = $('.header-search_container_toggled');
$('.toggle_search').on('click', function(){
	search_container.toggleClass('active-search');
	if (search_container.hasClass('active-search')) {
		search_container.find('.header-search_input').trigger('focus');
	}
})

const header = $('.main-header');

if (header.length) {
	const main_el = $('main');
	let timer_main;
	let prev = 0;
	if (ResizeObserver !== undefined) {
		const resizeObserver = new ResizeObserver(entries => {
			resizeHeader(entries[0].target.clientHeight, prev)
			
		});
	
		resizeObserver.observe(header[0]);
	}else{
		window.addEventListener('resize', function(e) {
			resizeHeader(header[0].clientHeight)
		}, true);

		$(window).trigger('resize');
	}

	function resizeHeader(clientHeight){
		if (prev !== clientHeight) {
			clearTimeout(timer_main);
			prev = clientHeight;
			timer_main = setTimeout(() => {
				main_el.css('padding-top', clientHeight + 30 + 'px')
			}, 200)
		}
	}
	
}



$("body").on('click', '.collection_action', function(){
	popupToggle($('.popup-collection'));
})

$("body").on('click', '.edit_collection', function(){
	popupToggle($('.popup-edit-collection'));
})

$('.mobile_toggle_menu').on('click', () => {
	$('.mobile_menu').toggleClass('active_menu');
	toggleBg()
})

$('.list_wrap_action').on('click', function(){
	$(this).toggleClass('active-list')
})
const filter_toggle = $('.filter_toggle');
if (filter_toggle.length) {
	filter_toggle.on('click', function(){
		$('.gallery_section-filter_container').toggleClass('active-filter')
	})
}

const steps_containers = $('.steps_container');
if (steps_containers.length) {
	steps_containers.each(function(){
		$(this).find('.next_step').on('click', function(){
			$(this).parents('.step_container').removeClass('active_step').next('.step_container').addClass('active_step');
		})
		$(this).find('.prev_step').on('click', function(){
			$(this).parents('.step_container').removeClass('active_step').prev('.step_container').addClass('active_step');
		})
	})
}

$('.header-search').on('submit', function(e){
	e.preventDefault();
	if ($(this).find('.header-search_input').val()) {
		location.href = `/search/${$(this).find('.header-search_input').val()}`;
	}else{
		location.href = '';
	}
})

const color_category = $('.color_category');
if (color_category.length) {
	setTimeout(() => {
		color_category.each(function(){
			$(this).css({
				backgroundColor: `${randomColor()}`
			})
		})
	}, 1000);
}

//functions
const dark_bg = $('.dark_bg');

dark_bg.on('click', function(){
	$('.mobile_menu').removeClass('active_menu');
	toggleBg('removeClass')
});

const cookie_alert = $('.cookie_alert');
if (cookie_alert.length) {

	function setCookie(cName, cValue, expDays) {
        let date = new Date();
        date.setTime(date.getTime() + (expDays * 24 * 60 * 60 * 1000));
        const expires = "expires=" + date.toUTCString();
        document.cookie = cName + "=" + cValue + "; " + expires + "; path=/";
	}

	cookie_alert.find('.accept-cookies').on('click', function(){
		cookie_alert.addClass('accepted');
		setCookie('cookies-success', 1, 30);
	})
	setTimeout(() => cookie_alert.addClass('to_top'), 1000);
}

const copy_forms = $('.form-copy-link');

if (copy_forms.length) {
	copy_forms.on('click', function(){
		const $this = $(this);
		const textToCopy = $(this).find('.form-copy-link_text');
		
		navigator.clipboard.writeText(textToCopy.text().trim())
			.then(() => {
				$this.addClass('copied');
				setTimeout(() => $this.removeClass('copied'), 2500);
			})
	})
}


export function toggleBg(actionClass = 'toggleClass'){
	dark_bg[actionClass]('active-bg')
}

